.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);

    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-inner {
    position: relative;
    padding: 3px;
    width: 35%;
    height: 93%;
    max-width: 640px;
    background-color: #FFF;
    border-radius: 20px;
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}
